.register-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #667eea, #764ba2);
  font-family: 'poppins', sans-serif;
}

.register-page h1{
  color: rgb(65, 65, 65);
  font-size: 18px;
}

.register-container {
  background-color: white;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  max-width: 400px;
  width: 100%;
}

.register-form {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}

.input-container {
  position: relative;
}

.register-form input {
  margin-bottom: 0.5rem;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  box-sizing: border-box;
}

.password-toggle {
  position: absolute;
  top: 50%;
  right: 12px;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  color: #666;
}

.register-btn {
  padding: 0.5rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.login-link {
  font-size: 0.9rem;
  color: #666;
}

.login-link a {
  color: #007bff;
  text-decoration: none;
}
