/* CategoryPieChart.css */

.pie-chart-container {
    margin-top: 20px;
    text-align: center;
  }
  
  .recharts-wrapper {
    display: inline-block;
  }
  
  .recharts-surface {
    display: inline-block;
  }
  
  .recharts-responsive-container {
    display: inline-block;
  }
  
  .recharts-pie-labels {
    font-size: 12px;
    fill: white;
  }
  
  .recharts-legend {
    margin-top: 10px;
  }
  
  .recharts-legend-item-text {
    font-size: 14px;
    fill: #333;
  }
  