@import url('https://fonts.googleapis.com/css?family=Poppins');

.chart-container {
  height: 400px;
  width: 100%;
  border-radius: 12px;
  margin-bottom: 100px;
  margin-top: 50px;
}

.toggle-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.toggle-label {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 20px; /* Increase margin for more space between toggles */
  font-size: 12px; /* Smaller text */
  color: #777; /* Light gray text color */
  font-family: 'Poppins', sans-serif;
}

.toggle-input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-slider {
  position: relative;
  display: inline-block;
  width: 50px; /* Adjust the width to make the toggle smaller */
  height: 28px; /* Adjust the height to make the toggle smaller */
  background-color: #ccc;
  transition: .4s;
  border-radius: 28px; /* Adjust to match the height */
  margin-bottom: 8px; /* Space between the slider and the text */
  cursor: pointer;
}

.toggle-input:checked + .toggle-slider {
  background-color: #85d485; /* Light green color */
}

.toggle-slider:before {
  position: absolute;
  content: "";
  height: 20px; /* Adjust the size of the toggle knob */
  width: 20px; /* Adjust the size of the toggle knob */
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

.toggle-input:checked + .toggle-slider:before {
  transform: translateX(22px); /* Adjust to match the width */
}


.custom-tooltip {
  background-color: #363636b9;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  color: rgb(255, 255, 255);
}

.custom-tooltip .label {
  font-size: 14px;
}



@media (max-width: 600px) {

  .chart-container {
    height: 400px;
    width: 115%;
    margin-left: -40px;
    margin-bottom: 100px;
    margin-top: 50px;
  }
  

  .toggle-slider {
    width: 40px; /* Justerad bredd för mindre skärmar */
    height: 20px; /* Justerad höjd för mindre skärmar */
  }

  .toggle-slider:before {
    height: 14px; /* Justerad storlek för knappen för mindre skärmar */
    width: 14px; /* Justerad storlek för knappen för mindre skärmar */
    left: 3px; /* Justerad position för knappen för mindre skärmar */
    bottom: 3px; /* Justerad position för knappen för mindre skärmar */
  }
}
