.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #667eea, #764ba2);
  font-family: 'poppins', sans-serif;
}

.login-container {
  background-color: white;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  max-width: 400px;
  width: 100%;
}

.login-container h2 {
  color: rgb(65, 65, 65);
  font-size: 18px;
}

.login-form {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}

.input-container {
  position: relative;
}

.login-form input {
  margin-bottom: 0.5rem;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  box-sizing: border-box;
}

.password-toggle {
  position: absolute;
  top: 50%;
  right: 12px;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  color: #666;
}

.login-btn {
  padding: 0.5rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.register-link {
  font-size: 0.9rem;
  color: #666;
}

.register-link a {
  color: #007bff;
  text-decoration: none;
}

.popup {
  position: fixed;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  background-color: #ffcccc;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.popup.show {
  opacity: 1;
  visibility: visible;
}

.popup p {
  margin: 0;
  padding: 0;
  color: #ff0000;
  text-align: center;
}

button {
  border: none;
  background-color: #007bff;
  cursor: pointer;
  color: #ffffff;
  padding: 5px;
  border-radius: 10px;
}
