.budget-page {
  margin: 0 auto;
  padding: 20px;
  font-family: 'Poppins', Tahoma, Geneva, Verdana, sans-serif;
  width: 600px;
}

.income-expense-containers {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
 
}

.income-container {
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
  border-radius: 20px;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 48%;
  cursor: pointer;
  font-size: 12px;
  color:#1f1f1f;
}

.expense-container {
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 20px;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 48%;
  cursor: pointer;
  margin-left: 10px;
  font-size: 12px;
  color:#1f1f1f;
  
}

.income-container h3,
.expense-container h3 {
  margin: 0;
  font-weight: normal;
}

.income-container button,
.expense-container button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 20px;
  color: #28a745;
}

.expense-container button {
  color: #dc3545;
}

.panel {
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  
}

.panel-header h3 {
  margin: 0;
  font-size: 14px;
  color:#1f1f1f;
}

.close-btn {
  color: #dc3545;
  font-size: 20px;
  cursor: pointer;
}

.panel form {
  display: flex;
  flex-direction: column;
}

.panel form input {
  margin-bottom: 10px;
  padding: 8px;
  font-size: 16px;
  border-radius: 4px;
  border: 1px solid #ced4da;
}

.panel form button {
  padding: 10px;
  font-size: 16px;
  background-color:#007bff;
  color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
  width: 100px;
}

.panel form button:hover {
  background-color: #1e7e34;
}

.transactions {
  margin-top: 20px;
}



.transactions h3 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 16px;
  color:#353535;
  font-weight: normal;
}

.transactions ul {
  list-style-type: none;
  padding: 0;
}

.transactions li {
  margin-bottom: 10px;
  padding: 10px;
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  border-left: 2px solid;
  color: rgb(65, 65, 65);
  font-size: 14px;
}

.transactions li.income {
  border-left: 5px solid green;
}

.transactions li.expense {
  border-left: 5px solid red;
}

.transactions li .delete-btn {
  float: right;
  margin-left: 10px;
  color: #dc3545;
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
  margin-top: -5px;
}

.totals {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.total-item {
  border-radius: 4px;
  padding: 10px;
  text-align: center;
  width: 29%;
}

.total-item h4 {
  margin-top: 0;
  margin-bottom: 5px;
  font-size: 14px;
  color:#353535;
}

.total-item p {
  margin: 0;
  font-size: 14px;
  font-weight: bold;
  color:#353535;
}

.remaining-bar {
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  margin-bottom: 20px;
  position: relative;
  height: 20px;
  display: flex;
  align-items: center; /* Centrerar vertikalt */
}

.bar {
  height: 100%;
  background-color: #85d485;
  border-radius: 4px;
  transition: width 0.3s ease;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center; /* Centrerar vertikalt */
}

.bar-text {
  color: #fff;
  font-weight: bold;
  text-align: center;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}





.bar-label {
  text-align: center;
  font-size: 13px;
  color: #6c757d;
  margin-bottom: 30px;
  margin-top: -15px;
}


.edit-btn {
  margin-left: 8px;
  cursor: pointer;
  color: #575757;
  float: right;
  margin-top: 1px;
}

.edit-btn:hover {
  color: #555;
}

.tooltip-icon {
  margin-left: 8px;
  cursor: pointer;
  color: #888;
}

.tooltip-icon:hover {
  color: #555;
}


#categoryTooltip {
  font-size: 12px;
  width: 50%;
}


@media (max-width: 768px) {
  .budget-page {
    width: 98%;
    padding:0;
  }
  
}




