@import url('https://fonts.googleapis.com/css?family=Poppins');

.main-page {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  height: auto;
  font-family: 'Poppins', sans-serif;
}

.content {
  display: flex;
  flex: 1;
}

.main-container {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.navbar {
  background-color: #272c70;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
}

.navbar-left {
  display: flex;
  align-items: center;
}

.navbar-brand {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.navbar-toggle {
  display: none;
  align-items: center;
  cursor: pointer;
  font-size: 18px;
  margin-left: 20px;
}

.navbar-links {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.navbar-links.open {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  background-color: #272c70;
}

.navbar-link {
  display: flex;
  align-items: center;
  color: #fff;
  text-decoration: none;
  padding: 10px;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.navbar-link:hover {
  background-color: #444;
}

.navbar-icon {
  margin-right: 5px;
}

.navbar-text {
  display: block;
}

.user-section {
  display: flex;
  align-items: center;
  margin-left: auto;
}

.navbar-mobile-user-section {
  display: none;
  flex-direction: column;
  align-items: center;
}

.welcome-message {
  font-size: 18px;
  margin-right: 10px;
}

.logout-button {
  background-color: #dc3545;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  display: flex;
  align-items: center;
}

.logout-button:hover {
  background-color: #c82333;
}

.budget-container {
  flex: 1;
  padding: 20px;
}

.budget-container h1 {
  text-align: center;
}

.month-selector {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.month-selector label {
  font-size: 14px;
  margin-bottom: 10px;
}

.month-selector input[type="month"] {
  padding: 10px;
  font-size: 14px;
  border: 1px solid #3a3a3a;
  border-radius: 20px;
  font-family: 'Poppins', sans-serif;
  color: #3a3a3a;
}

input::-webkit-datetime-edit-day-field:focus,
input::-webkit-datetime-edit-month-field:focus,
input::-webkit-datetime-edit-year-field:focus {
  background-color: rgb(104, 149, 175);
  color: white;
  outline: none;
  padding: 10px;
}

@media (max-width: 768px) {
 
  .navbar-toggle {
    display: flex;
  }
  .navbar-links {
    display: none;
    flex-direction: column;
    width: 100%;
    margin-top: -12px;
    margin-left: -0px;
  }
  .navbar-links.open {
    display: flex;
  }
  .user-section {
    display: none;
  }
  .navbar-mobile-user-section {
    display: flex;
    padding: 10px 0;
    border-top: 1px solid #444;
    width: 100%;
    justify-content: center;
  }
}
